import React, { useEffect, useState } from "react";
import { useStaticConfig } from "../../../plugins/remote-content/src/components/ppm/static-content/useStaticConfig";

const pretty = (v: any) => (
  <pre
    style={{
      fontSize: 12,
      maxHeight: 280,
      overflow: "auto",
      background: "#0f172a",
      color: "#e5e7eb",
      padding: 16,
      borderRadius: 8,
      border: "1px solid #334155",
      fontFamily: "JetBrains Mono, Menlo, monospace",
    }}
  >
    {JSON.stringify(v, null, 2)}
  </pre>
);

export default function ResolverInspectorPage() {
  const [mounted, setMounted] = useState(false);
  const [remote, setRemote] = useState<any>(null);
  const [cms, setCms] = useState<any>(null);
  const [resolved, setResolved] = useState<any>(null);

  useStaticConfig(
    "https://bodephatquoc.website.wiki/static-content/static-config.js"
  );

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    if (!mounted) return;

    setRemote((window as any).MAIN_CONTENT || null);

    const cmsRaw = localStorage.getItem("sbu-cms-data");
    setCms(cmsRaw ? JSON.parse(cmsRaw) : null);

    setResolved((window as any).__RESOLVED_CONTENT__ || null);
  }, [mounted]);

  if (!mounted) {
    return null;
  }

  const remoteVersion = remote?.VERSION ?? null;
  const cmsVersion = cms?.meta?.version ?? null;
  const localVersion = 0;

  const sourceWinner =
    cmsVersion !== null && cmsVersion >= (remoteVersion ?? -Infinity)
      ? "admin-draft"
      : remoteVersion !== null
      ? "remote-config"
      : "local-hardcoded";

  const versionWinner =
    cmsVersion !== null && cmsVersion >= (remoteVersion ?? -Infinity)
      ? cmsVersion
      : remoteVersion ?? localVersion;

  return (
    <div
      style={{
        fontFamily:
          "Inter, -apple-system, system-ui, Segoe UI, Roboto, sans-serif",
        padding: 32,
        background: "#0b1220",
        color: "#e5e7eb",
        minHeight: "100vh",
      }}
    >
      <header
        style={{
          marginBottom: 32,
          paddingBottom: 16,
          borderBottom: "1px solid #1e293b",
        }}
      >
        <h1 style={{ margin: 0, color: "#facc15" }}>üß™ Resolver Inspector</h1>
        <p style={{ opacity: 0.75 }}>
          Visual explanation of how content precedence was resolved at runtime.
        </p>
      </header>

      <section style={{ marginBottom: 32 }}>
        <h2 style={{ marginBottom: 12 }}>1) Available Sources</h2>

        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
            background: "#0f172a",
            borderRadius: 8,
            overflow: "hidden",
          }}
        >
          <thead>
            <tr style={{ background: "#111827", color: "#facc15" }}>
              <th style={{ padding: 12, textAlign: "left" }}>Source</th>
              <th style={{ padding: 12 }}>Exists?</th>
              <th style={{ padding: 12 }}>Version</th>
              <th style={{ padding: 12 }}>Has CONTENT?</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td style={{ padding: 12 }}>remote-config</td>
              <td style={{ padding: 12 }}>{String(!!remote)}</td>
              <td style={{ padding: 12 }}>{remoteVersion}</td>
              <td style={{ padding: 12 }}>{String(!!remote?.CONTENT)}</td>
            </tr>
            <tr>
              <td style={{ padding: 12 }}>admin-draft</td>
              <td style={{ padding: 12 }}>{String(!!cms)}</td>
              <td style={{ padding: 12 }}>{cmsVersion}</td>
              <td style={{ padding: 12 }}>{String(!!cms)}</td>
            </tr>
            <tr>
              <td style={{ padding: 12 }}>local-hardcoded</td>
              <td style={{ padding: 12 }}>true</td>
              <td style={{ padding: 12 }}>0</td>
              <td style={{ padding: 12 }}>true</td>
            </tr>
          </tbody>
        </table>
      </section>

      <section
        style={{
          marginBottom: 32,
          padding: 16,
          background: "#064e3b",
          borderRadius: 8,
        }}
      >
        <h2 style={{ marginTop: 0 }}>2) üèÜ Winner</h2>

        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
          }}
        >
          <thead>
            <tr>
              <th style={{ padding: 8 }}>Winning Source</th>
              <th style={{ padding: 8 }}>Winning Version</th>
              <th style={{ padding: 8 }}>UNDER_CONSTRUCTION</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td style={{ padding: 8, fontWeight: 700 }}>{sourceWinner}</td>
              <td style={{ padding: 8 }}>{versionWinner}</td>
              <td style={{ padding: 8 }}>
                {String(resolved?.UNDER_CONSTRUCTION)}
              </td>
            </tr>
          </tbody>
        </table>
      </section>

      <section>
        <h2 style={{ marginBottom: 12 }}>3) Raw Payloads</h2>

        <div style={{ display: "grid", gap: 20 }}>
          <div>
            <h3>Remote (window.MAIN_CONTENT)</h3>
            {pretty(remote)}
          </div>

          <div>
            <h3>CMS Draft (localStorage)</h3>
            {pretty(cms)}
          </div>

          <div>
            <h3>Resolved Output</h3>
            {pretty(resolved)}
          </div>
        </div>
      </section>
    </div>
  );
}